import React, { Component } from "react";

export default class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      hasError: false
    };
  }
  componentDidCatch() {
    this.setState({
      hasError: true
    });
  }
  render() {
    return this.state.hasError ? (
      <p style={{ marginTop: "40vh", textAlign: "center" }}>
        Something went wrong
      </p>
    ) : (
      this.props.children
    );
  }
}
