export class DataServiceSingleShow {
  constructor(data) {
    const { id, name, officialSite, premiered, rating, summary } = data;
    const { cast, seasons } = data._embedded;
    // converting http => https

    const splittedOriginal = data.image.original.split("://");
    const finalSrcOriginal = splittedOriginal[0] + "s://" + splittedOriginal[1];

    /////////////
    this.seasons = seasons;
    this.summary = summary;
    this.imageOriginal = finalSrcOriginal;
    this.id = id;
    cast.forEach(person => {
      const imageSplitted = person.person.image.medium.split("://");
      const imageHttps = imageSplitted[0] + "s://" + imageSplitted[1];
      person.person.image.medium = imageHttps;
    });
    this.cast = cast;
  }
}

export class DataService {
  constructor(data) {
    const { id, name, summary } = data;
    const { medium } = data.image;

    this.id = id;
    this.title = name;
    this.summary = summary;
    const imageSplitted = medium.split("://");
    const image = imageSplitted[0] + "s://" + imageSplitted[1];
    this.image = image;
  }
}
